import { useContext } from 'react';
import { findTwoHighestIndices } from './HighScores.js';
import { PlayerContext } from '../../context/PlayerContext';

const SurfScoringLogic = ({
    index, 
    completed
}) => {

    const {
        players
    } = useContext(PlayerContext);

    const playerId = index;
    const scoreDifference = () => {
        const newPlayerId = (playerId === 0) ? 0 : (playerId - 1);
        if (players[newPlayerId].surfScore) {
            return Number(players[newPlayerId].surfScore - players[(playerId === 0) ? 1 : (playerId)].surfScore);
        }
        return 0;
    }
    const difference = (scoreDifference() < 0) ? (-1 * scoreDifference()) : scoreDifference();
    const winningScore = Number(players[(playerId === 0) ? 0 : ((playerId < 3) ? (playerId - 1) : 1)].surfScore);
    const highestScoresIndices = (players[playerId]) ? findTwoHighestIndices(players[playerId].surfScores):[];
    const losersHeighestScore = (players[playerId]) ? players[playerId].surfScores[highestScoresIndices[0]]:[];
    const need = (winningScore - losersHeighestScore) + .01;
    const winsBy = <div>
                        <div>{`${(completed) ? 'Won' : 'Wins'} by`}</div>
                        <div>{difference.toFixed(2)}</div>
                    </div>;
    const needs = <div>
                        <div>{`${(completed) ? 'Needed' : 'Needs'}`}</div>
                        <div>{need.toFixed(2)}</div>
                    </div>;
    if (playerId === 0) {
        return winsBy
    }
    return needs

}
export default SurfScoringLogic;