import React, { useState, useEffect, useContext } from 'react';
import getKey from '../../utils/KeyGenerator';
import golfScoring from './golfScoring';
import parsDefault from './parsDefault';
import ScoreCardGolf from './ScoreCardGolf';
import initializeData from '../../utils/InitializeData';
import { GolfContext } from '../../context/GolfContext';

const GolfScores = ({ playerIndex, updateScores }) => {
    
    const {
        golfPars,
        setPars,
    } = useContext(GolfContext);

    const editGolfPar = (hole) => {
        const newPars = [...golfPars];
        const newPar = prompt(`Enter par for hole ${hole+1}:`, golfPars[hole]);
        newPars[hole] = newPar;
        setPars(newPars);
    }
    const buttonClass = 'bg-darker';
    const getButtonClass = 'glassy button flex3Column p-5 r-10 m-1' + buttonClass;
    const golfScores = () => golfScoring.map((hole, index) => <div key={getKey(hole)} className={`containerDetail mb-55 scrollSnapTop pt-1 ${getButtonClass}`}>
        <div className={`contentCenter bg-darker r-10 m-5 mt-15`}>
            <div>
                <span className='copyright greet color-yellow'>
                    Hole:
                </span>
                <span className='size40 bold pt-5 pl-15 pr-15 bg-darker r-10 color-white'>
                    {index + 1}
                </span>
                <span className='copyright greet color-yellow'>
                    Par:
                </span>
                <span className='size20 bold color-lite mt--5 button' onClick={() => editGolfPar(index)}>
                    {golfPars[index]}
                </span>
            </div>
            <ScoreCardGolf
                playerIndex={playerIndex}
                scoreIndex={index}
                updateScores={updateScores}
            />
        </div>
    </div>);

    return golfScores();
}

export default GolfScores;