import React, { useState, useEffect } from 'react';
import initializeData from '../../tracker/initializeData';
import { games, gameIcons } from './games';
import getMedal from './GetMedal';

const PlayerStandings = ({
    game
}) => {

    const [currentGame, setCurrentGame] = useState();
    const [standings, setStandings] = useState([]);
    const [dataset, setDataset] = useState();

    useEffect(() => {
        //const game = localStorage.getItem('game');
        setCurrentGame(game);
    }, [game]);
    
    useEffect(() => {
        console.log(`currentGame: ${currentGame}`);
        const dataset = localStorage.getItem(`${currentGame}Games`);
        setDataset(JSON.parse(dataset));
    }, [currentGame]);
    
    useEffect(() => {
        if (dataset) {
            console.log(`dataset: ${JSON.stringify(dataset, null, 2)}`);
            const playerWins = {};
            dataset.forEach((game) => {
                const winner = game.players.reduce((prev, current) => (prev.score > current.score) ? prev : current);
                if (playerWins[winner.name]) {
                    playerWins[winner.name] += 1;
                } else {
                    playerWins[winner.name] = 1;
                }
            });

            const sortedStandings = Object.entries(playerWins)
                .map(([name, wins]) => ({ name, wins }))
                .sort((a, b) => b.wins - a.wins);

            setStandings(sortedStandings);
        }
    }, [dataset]);
/*
    useEffect(() => {
        const games = initializeData(`${currentGame}Games`, []);
        if (games.length > 0) {
            setDataset(games);
        }
    }, [currentGame]);

    useEffect(() => {
        const playerWins = {};
        if (dataset !== undefined) {
            dataset.forEach((game) => {
                const winner = game.players.reduce((prev, current) => (prev.score > current.score) ? prev : current);
                if (playerWins[winner.name]) {
                    playerWins[winner.name] += 1;
                } else {
                    playerWins[winner.name] = 1;
                }
            });
            const sortedStandings = Object.entries(playerWins)
                .map(([name, wins]) => ({ name, wins }))
                .sort((a, b) => b.wins - a.wins);

            console.log(`dataset: ${JSON.stringify(dataset, null, 2)}`);
            console.log(`sortedStandings: ${JSON.stringify(sortedStandings, null, 2)}`);
            setStandings(sortedStandings);
        }
    }, [dataset]);
*/
    return (
        <div>
            <div className='containerBox'>
                <div className='containerBox'>
                    {gameIcons[String(game).replace(' ','')]} 
                </div>
                {currentGame} 
            </div>
            <div>
                {standings.map((player, index) => (
                    <div key={index} className='containerBox'>
                        <div className='p-5 ht-75 centeredContent'>
                            <div>
                                {getMedal(0)} x {player.wins}
                            </div>
                            <div>{player.name.split(' ')[0]}</div>
                            <div>{player.name.split(' ')[1]}</div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default PlayerStandings;