import React, { useState, useEffect, useRef } from 'react';
import getKey from '../utils/KeyGenerator';
import WaveUtils from '../wavefinder/WaveUtils';
import calculateDistance from '../location/CalculateDistance';
import NewTide from './NewTide';
import ConditionsDashboard from './ConditionsDashboard';
import SurfLocation from './SurfLocation';
import { getSurfSpots } from './SurfSpots';
import directionObject from './DirectionObject';
import ConditionsSelectors from './ConditionsSelectors';
//import Geolocator from '../utils/Geolocator';
import Location from '../utils/Location'
import useOceanData from './useOceanData';
import useCurrentTime from '../utils/useCurrentTime';
import ConditionsContext from '../context/ConditionsContext';
import ArrowsNorthSouth from './ArrowsNorthSouth';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import icons from '../utils/icons';
import settings from '../../assets/images/settings.png';
//import RssFeedComponent from './RssFeedComponent';
import validate from '../utils/validate';
import initializeData from '../utils/InitializeData';

const Waves = () => {

    const locationScrollerRef = useRef(null);
    const northRef = useRef(null);
    const southRef = useRef(null);
    const northSouthRef = useRef(null);
    const getLocal = (item) => initializeData(item, null);
    const getProps = (item) => item;

    const getDefault = (item) => (getLocal(item) === null)
        ? getProps(item)
        : getLocal(item);

    const getSwell1Direction = () => initializeData('swell1Direction', 'SSW');
    const getSwell2Direction = () => initializeData('swell2Direction', 'SSW');
    const [locations, setLocations] = useState(getSurfSpots());
    const [edit, setEdit] = useState(false);
    const getLocalData = (localItem) => initializeData(localItem, null);
    const collapseStateInit = (localItem) => getLocalData(localItem) ? getLocalData(localItem) === 'true' : true;
    const [locationCollapse, setLocationCollapse] = useState(collapseStateInit('locationCollapse'));
    const [matchCollapse, setMatchCollapse] = useState(collapseStateInit('matchCollapse'));
    const [gpsCollapse, setGpsCollapse] = useState(true);
    const [count, setCount] = useState(0);

    const [status, setStatus] = useState({
        module: 'Waves',
        pause: true,
        date: new Date(),
        //edit: false,
        tide: getDefault('tide'),
        stars: getDefault('stars'),
        waterTemp: '66.2',
        swell1Height: '2.0',
        swell1Interval: '17 seconds',
        swell1Direction: getSwell1Direction(),
        swell2Height: '2.0',
        swell2Interval: '9 seconds',
        swell2Direction: getSwell2Direction(),
        swell1Angle: directionObject[getSwell1Direction()],
        swell2Angle: directionObject[getSwell2Direction()],
        //swell1Direction: getDefault('swell1Direction'),
        //swell2Direction: getDefault('swell2Direction'),
        //swell1Angle: getDefault('swell1Angle'),
        //swell2Angle: getDefault('swell2Angle'),
        //swell1Height: getDefault('swell1Height'),
        //swell2Height: getDefault('swell2Height'),
        //swell1Interval: getDefault('swell1Interval'),
        //swell2Interval: getDefault('swell2Interval'),
        windDirection: getDefault('windDirection'),
        distance: getDefault('distance'),
        isSwell1: (initializeData('isSwell1', null) === true) ? true : false,
        isSwell2: (initializeData('isSwell2', null) === true) ? true : false,
        isTide: (initializeData('isTide', null) === true) ? true : false,
        isWind: (initializeData('isWind', null) === true) ? true : false,
        //locations: getLocalLocations(),
        matches: [],
        init: false
    });
    const [buttonsVisible, setButtonsVisible] = useState(false);
    const toggleButtons = () => {
        setButtonsVisible(!buttonsVisible);
    };
    const time = useCurrentTime();
    const startTime = time[0].startTime;
    const endTime = time[0].endTime;
    const tideNowLink = `https://api.tidesandcurrents.noaa.gov/api/prod/datagetter?begin_date=${startTime}&end_date=${endTime}&station=9410660&product=water_level&datum=mllw&units=english&time_zone=lst_ldt&application=web_services&format=json`;
    const uriMLL = `https://api.tidesandcurrents.noaa.gov/api/prod/datagetter?product=predictions&application=NOS.COOPS.TAC.WL&begin_date=${startTime}&end_date=${endTime}&datum=MLLW&station=9410230&time_zone=lst_ldt&units=english&interval=hilo&format=json`;
    // eslint-disable-next-line
    const [retry, setRetry] = useState('');
    const data = useOceanData('tides', uriMLL, '', setRetry);
    // eslint-disable-next-line
    const tideNow = useOceanData('tide', tideNowLink, '', setRetry);

    const tick = () => {
        console.log(`tick => pause: ${status.pause}`)
        if (status.pause === false) {
            setStatus(prevState => ({
                ...prevState,
                date: new Date(),
                pause: true
            }));
        }
    }
    const addWave = () => {
        const swells = [];
        const winds = [];
        const tides = [];
        const waveLocations = [...locations];
        console.log(`addWave => waveLocations: ${JSON.stringify(waveLocations, null, 2)}`)
        let i=0;
        let wave = prompt('wave: ', '');
        const latitude = prompt('lat: ', initializeData('latitude', null));
        const longitude = prompt('long: ', initializeData('longitude', null));
        const swellCount = prompt('swell count: ', '');
        for (i=0; i<swellCount; i++) {
            swells[i] = prompt('swell direction', '').toLocaleUpperCase();
        }
        const windCount = prompt('wind count: ', '');
        for (i=0; i<windCount; i++) {
            winds[i] = prompt('wind direction', '').toLocaleUpperCase();
        }
        const tideCount = prompt('tide count: ', '');
        for (i=0; i<tideCount; i++) {
            tides[i] = prompt('tide direction', '').toLocaleLowerCase();
        }
        const getObj = () => {
            return {
                name: wave,
                latitude: latitude,
                longitude: longitude,
                swell: swells,
                wind: winds,
                tide: tides
            }
        }
        waveLocations.push(getObj());
        localStorage.setItem('locations', JSON.stringify(waveLocations));
        setLocations(waveLocations);
        updateLocations(waveLocations, (waveLocations.length-1));
    }
    /*
    useEffect(() => {
        const timerID = setInterval(
            () => tick(),
            30000000
        );
        return function cleanUp() {
            clearInterval(timerID);
        }
    }, []);
    useEffect(() => {
        localStorage.setItem('gpsCollapse', gpsCollapse);
    }, [gpsCollapse]);
    useEffect(() => {
        localStorage.setItem('locationCollapse', locationCollapse);
    }, [locationCollapse]);
    useEffect(() => {
        localStorage.setItem('matchCollapse', matchCollapse);
    }, [matchCollapse]);
    useEffect(() => {
        localStorage.setItem('edit', false);
    }, []);

    useEffect(() => {
        localStorage.setItem('locations', JSON.stringify(locations));
    }, [locations]);
    useEffect(() => {
        localStorage.setItem('isSwell1', status.isSwell1);
        localStorage.setItem('isSwell2', status.isSwell2);
        localStorage.setItem('stars', status.stars);
        localStorage.setItem('isWind', status.isWind);
        localStorage.setItem('isTide', status.isTide);
        localStorage.setItem('swell1Angle', status.swell1Angle);
        localStorage.setItem('swell1Direction', (status.swell1Direction !== 'X')?status.swell1Direction:getSwell1Direction());
        localStorage.setItem('swell1Height', status.swell1Height);
        localStorage.setItem('swell1Interval', status.swell1Interval);
        localStorage.setItem('swell2Angle', status.swell2Angle);
        localStorage.setItem('swell2Direction', (status.swell2Direction !== 'X')?status.swell2Direction:getSwell2Direction());
        localStorage.setItem('swell2Height', status.swell2Height);
        localStorage.setItem('swell2Interval', status.swell2Interval);
        localStorage.setItem('distance', status.distance);
        if (validate(status.height) !== null) {
            localStorage.setItem('height', status.height);
        }
        localStorage.setItem('tide', status.tide);
        localStorage.setItem('windDirection', status.windDirection);
    }, [status]);
    */
    const currentPositionExists = () => (status.longitude) ? true : false;
    const updateCurrentLocation = ({ 
        latitude, 
        longitude 
    }) => {
        console.log(`UPDATING CURRENT POSITION ======> longitude: ${longitude} latitude: ${latitude}`)
        if (currentPositionExists()) {

            if ((Math.abs(status.longitude - longitude) > .0001) || (Math.abs(status.latitude - latitude) > .0001) || (status.init === false)) {
                console.log(`updateCurrentLocation => satus coords ^^^^^^^^^^^ ${longitude}, ${latitude}`)
                setStatus(prevState => ({
                    ...prevState,
                    longitude,
                    latitude,
                    init: true
                }));
            }
        } else if ((Math.abs(Number(initializeData('longitude', null)) - longitude) > .000003) || (Math.abs(initializeData('latitude', null) - latitude) > .000003)) {
            console.log(`updateCurrentLocation => local coords ^^^^^^^^^^^ ${longitude}, ${latitude}`)
            setStatus(prevState => ({
                ...prevState,
                longitude,
                latitude,
                init: true
            }));
        }
    }
    const updateLocations = (locations, index) => {
        console.log(`updateLocations1 => ${JSON.stringify(locations, null, 2)}`);
        console.log(`updateLocations2 => ${initializeData('locations', null) }`);
        setEdit(!edit);
        setLocations(locations);
    };
    const getDefaultHeights = (tideSelected) => {
        if (tideSelected === 'high') {
            return 5;
        } else if (tideSelected === 'medium') {
            return 3;
        }
        return 0;
    }
    const handleWindCheck = () => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            isWind: !status.isWind
        }));
    }
    const handleTideCheck = () => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            isTide: !status.isTide
        }));
    }
    const handleSwellCheck = (id) => {
        if (id === '1') {
            setStatus(prevState => ({
                ...prevState,
                pause: true,
                isSwell1: !status.isSwell1
            }));
        } else {
            setStatus(prevState => ({
                ...prevState,
                pause: true,
                isSwell2: !status.isSwell2
            }));
        }
    }
    // eslint-disable-next-line
    const handleSwellSelection = (id, groupTitle, label, selected) => {
        // eslint-disable-next-line
        const swellAngle = directionObject[selected];
        if (id === '1') {
            setStatus(prevState => ({
                ...prevState,
                pause: true,
                swell1Direction: selected,
                swell1Angle: swellAngle
            }));
        } else {
            setStatus(prevState => ({
                ...prevState,
                pause: true,
                swell2Direction: selected,
                swell2Angle: swellAngle
            }));
        }
    }
    const handleSwell1Selection = (groupTitle, label, selected) => {
        const swell1Angle = directionObject[selected];
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            swell1Direction: selected,
            swell1Angle: swell1Angle
        }));
    }
    const handleSwell2Selection = (groupTitle, label, selected) => {
        const swell2Angle = directionObject[selected];
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            swell2Direction: selected,
            swell2Angle: swell2Angle
        }));
    }
    const handleSwell1Angle = (groupTitle, label, selected) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            swell1Angle: selected
        }));
    }
    const handleSwell2Angle = (groupTitle, label, selected) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            swell2Angle: selected
        }));
    }
    const handleSwell1Height = (groupTitle, label, selected) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            swell1Height: selected
        }));
    }
    const handleSwell2Height = (groupTitle, label, selected) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            swell2Height: selected
        }));
    }
    const handleSwell1Interval = (groupTitle, label, selected) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            swell1Interval: selected
        }));
    }
    const handleSwell2Interval = (groupTitle, label, selected) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            swell2Interval: selected
        }));
    }
    const handleStarSelection = (groupTitle, label, selected) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            stars: selected
        }));
    }
    const handleDistanceSelection = (event) => {
        const target = event.target;
        setStatus(prevState => ({
            ...prevState,
            distance: target.value
        }));
    }
    const pause = (event) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true
        }));
    }
    // eslint-disable-next-line
    const unpause = () => {
        setStatus(prevState => ({
            ...prevState,
            pause: true
        }));
    };
    const setTide = (tide, height) => {
        if (initializeData('tide', null) !== tide) {
            setStatus(prevState => ({
                ...prevState,
                tide: tide,
                height: height
            }));
        }
    }
    const tideDisplay = (display) => <NewTide
                                        display={display}
                                    />
    // eslint-disable-next-line
    const getState = (kind) => {
        const { swell1Direction, swell2Direction, tide, windDirection } = status;
        if (kind === 'swell1') {
            return swell1Direction;
        } else if (kind === 'swell2') {
            return swell2Direction;
        } else if (kind === 'tide') {
            return tide;
        } else if (kind === 'wind') {
            return windDirection;
        }
    }
    const handleTideSelection = (groupTitle, label, selected) => {
        const height = getDefaultHeights(selected);
        setTide(selected, height);
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            tide: selected,
            height: height
        }));
    }
    const setWind = (direction, angle, speed, gusts) => {
        setStatus(prevState => ({
            ...prevState,
            windDirection: direction,
            windAngle: Number(angle).toFixed(0),
            windSpeed: Number(speed).toFixed(0),
            windGusts: Number(gusts).toFixed(0)
        }));
    }
    const swell1Match = (item) => (item.swell.indexOf(status.swell1Direction) > -1) ? true : false;
    const swell2Match = (item) => (item.swell.indexOf(status.swell2Direction) > -1) ? true : false;
    const windMatch = (item) => (item.wind.indexOf(status.windDirection) > -1) ? true : false;
    const tideMatch = (item) => (item.tide.indexOf(status.tide) > -1) ? true : false;
    const getDistance = (item) => calculateDistance(status, item);
    const distanceRange = Number(status.distance);
    const regionMatch = (item) => ((getDistance(item) < distanceRange) || (distanceRange > 999999999999)) ? getDistance(item) : false;
    const match = (item) => {
        const matches = [];
        // eslint-disable-next-line
        let matchesCount = (swell1Match(item)) ? matches.push('swell1') : matches;
        matchesCount = (swell2Match(item)) ? matches.push('swell2') : matches;
        matchesCount = (windMatch(item)) ? matches.push('wind') : matches;
        // eslint-disable-next-line
        matchesCount = (tideMatch(item)) ? matches.push('tide') : matches;
        return matches;
    }
    // eslint-disable-next-line
    const statusClass = (status) => (status === true) ? 'color-neogreen' : 'color-yellow';
    // eslint-disable-next-line
    const subStatusClass = (status) => (status === true) ? 'color-orange' : 'color-yellow';
    const swell1Confirm = (matches) => ((status.isSwell1 && matches.includes('swell1')) || status.isSwell1 === false) ? true : false;
    const swell2Confirm = (matches) => ((status.isSwell2 && matches.includes('swell2')) || status.isSwell2 === false) ? true : false;
    const tideConfirm = (matches) => ((status.isTide && matches.includes('tide')) || status.isTide === false) ? true : false;
    const windConfirm = (matches) => ((status.isWind && matches.includes('wind')) || status.isWind === false) ? true : false;
    const showAll = false;
    const getMatchingLocation = (item) => {
        const matches = match(item);
        const inRegion = regionMatch(item);
        if ((swell1Confirm(matches) && swell2Confirm(matches) && tideConfirm(matches) && windConfirm(matches)) || showAll) {
            if (inRegion !== false) {
                if (matches.length >= Number(status.stars)) {
                    return <SurfLocation
                                key={getKey('link')}
                                state={status}
                                item={item}
                                matches={matches}
                                regionMatch={inRegion}
                                tideDisplay={tideDisplay}
                                updateLocations={updateLocations}
                                locationCollapse={locationCollapse}
                            >
                            </SurfLocation>
                }
            }
        }
    }
    const setScroll = () => {
        const height = () => (northRef.current) ? (northRef.current.clientHeight + northSouthRef.current.clientHeight) + 15 : 100;
        
        window.scrollTo({
            top: document.body.scrollHeight,
            behavior: 'smooth',
        });

        if (locationScrollerRef.current) {
            console.log(`setScroll => height: ${height()}`)
            console.log(`setScroll => locationScrollerRef.current.scrollHeight: ${locationScrollerRef.current.scrollHeight}`)
            locationScrollerRef.current.scrollTo({
                top: height(),
                left: 0,
                behavior: 'smooth',
            });
        }

        toggleButtons();
    }
    const currentTide = () => {

        window.scrollTo({
            top: 20,
            left: 0,
            behavior: 'smooth',
        });

        const localTide = Number(initializeData('tideData', null).data[initializeData('tideData', null).data.length - 1].v).toFixed(1);
        const waterLevel = (validate(tideNow[0].data) !== null) ? Number(tideNow[0].data[tideNow[0].data.length - 1].v).toFixed(1) : localTide;
        const getCurrentTide = (waterLevel > 3) ? "high" : (waterLevel < 2) ? "low" : "medium";
        localStorage.setItem('height', waterLevel);
        setTide(getCurrentTide);
        toggleButtons();
    }
    const currentConditions = () => {

        window.scrollTo({
            top: 650,
            left: 0,
            behavior: 'smooth',
        });

        const localTide = Number(initializeData('tideData', null).data[initializeData('tideData', null).data.length - 1].v).toFixed(1);
        const waterLevel = (validate(tideNow[0].data) !== null) ? Number(tideNow[0].data[tideNow[0].data.length - 1].v).toFixed(1) : localTide;
        const getCurrentTide = (waterLevel > 3) ? "high" : (waterLevel < 2) ? "low" : "medium";
        localStorage.setItem('height', waterLevel);
        setTide(getCurrentTide);
        setWindStatus(initializeData('wind', null));
        toggleButtons();
    }
    const conditionsEntry = () => {
        
        window.scrollTo({
            top: 1340,
            left: 0,
            behavior: 'smooth',
        });
        toggleButtons();
    }
    const getCount = () => initializeData('waveCount', 0);
    // eslint-disable-next-line
    const sortedSpots = () => {
        const latitude = (validate(status.latitude) !== null) ? status.latitude : 33.079940;
        const north = [];
        const south = [];
        locations.forEach((item) => {
            if (latitude > item.latitude) {
                south.push(item);
            } else {
                north.push(item);
            }
        });
        localStorage.setItem('scrollIndex', north.length)
        const northLocations = north.map((item) => <div key={getKey('northLocation')}>
                {getMatchingLocation(item)}
            </div>
        )
        const southLocations = south.map((item) => <div key={getKey('southLocation')}>
                {getMatchingLocation(item)}
            </div>
        )
        
        let sortedCount = 0;
        let sortedSouthCount = 0;
        let sortedNorthCount = 0;
        
        northLocations.forEach((loc) => {
            if (typeof loc === 'object') {
                sortedCount += 1;
                sortedNorthCount += 1;
            }
        });
        northLocations.forEach((loc) => {
            if (typeof loc === 'object') {
                sortedCount = sortedCount + 1;
                sortedSouthCount = sortedSouthCount + 1;
            }
        });
        //setCount(sortedCount);
        localStorage.setItem('waveCount', sortedCount);
        return <div id='locationScroller' ref={locationScrollerRef} className='locationScroller m-1 r-10' onScroll={() => console.log(`scrollTop: ${northSouthRef.current.clientHeight + northRef.current.clientHeight}`)}>
            <div id='north' ref={northRef}>
                {northLocations}
            </div>
            <div id='northSouth' ref={northSouthRef} className='flexContainer pt-10 bg-aqua r-20'>
                <div className='flex3Column'></div>
                <div className='flex3Column'>
                    <ArrowsNorthSouth 
                        north={sortedNorthCount}
                        south={sortedSouthCount}
                    />
                </div>
                <div className='flex3Column'></div>
            </div>
            <div id='south' ref={southRef} key={getKey('southLocations')}>
                {southLocations}
            </div>
        </div>;
    }
    const setWindStatus = (selected) => {
        setStatus(prevState => ({
            ...prevState,
            pause: true,
            windDirection: selected
        }));
    }
    const repositionButton = () => {
        return (
            <div onClick={() => setScroll()} className='size12 p-10 button r-5 color-lite bold bg-tinted m-1 box-shadow'>
                Closest {icons.waveSet} {icons.upDownArrow}
            </div>
        )
    }
    const conditionsButton = () => {
        return (
            <div onClick={() => conditionsEntry()} className='size12 p-10 button r-5 color-lite bold bg-tinted m-1 box-shadow'>
                Conditions{/*{icons.wave}{icons.moon}{icons.wind}*/}
            </div>
        )
    }
    const currentTideButton = () => {
        return (
            <div onClick={() => currentTide()} className='size12 p-10 button r-5 color-lite bold bg-tinted m-1 box-shadow'>
                Tide{/*{icons.moon}*/}
            </div>
        )
    }
    const handleEditToggle = () => {
        const edit = (initializeData('edit', null) === 'true') ? false : true;
        localStorage.setItem('edit', edit);
        setEdit(edit);
    }
    const currentButton = () => {
        return (
            <div onClick={() => currentConditions()} className='size12 p-10 button r-5 color-lite bold bg-tinted m-1 box-shadow'>
                Current{/*{icons.wind}{icons.tide}{icons.water}{icons.temperature}{icons.track}*/}
            </div>
        )
    }
    return (
        (retry !=='')
        ? <div>
                WATER TEMP: Error fetching data retry attempt {retry}
            </div>
        : <div className='r-10 p-5 mt--30 width-100-percent size25'>
            <div className='containerBox bold color-yellow bg-lite p-20'>
                <CollapseToggleButton
                    //title={`TIDE ${icons.moon}`}
                    //title={`TIDE ${Number(heightInit()).toFixed(1)}ft ${getCurrentTide}`}
                    title={'GPS'}
                    isCollapsed={gpsCollapse}
                    setCollapse={setGpsCollapse}
                    align='left'
                    icon={settings}
                />
            </div>
             {
                (gpsCollapse)
                ? null
                : <Location currentPositionExists={currentPositionExists} returnCoordinates={updateCurrentLocation} />
                    //{/*<Geolocator currentPositionExists={currentPositionExists} returnCurrentPosition={updateCurrentLocation} />*/}
             }       
            <div className='color-soft'>
                <ConditionsDashboard 
                    tideDisplay={tideDisplay} 
                    setWind={setWind} 
                    status={status}
                    setStatus={setStatus}
                />
                <ConditionsContext.Provider value={status}>
                    <ConditionsSelectors
                        status={status}
                        setTide={setTide}
                        setWind={setWind}
                        setWindStatus={setWindStatus}
                        handleTideCheck={handleTideCheck}
                        handleTideSelection={handleTideSelection}
                        handleWindCheck={handleWindCheck}
                        handleSwellCheck={handleSwellCheck}
                        handleSwell1Selection={handleSwell1Selection}
                        handleSwell2Selection={handleSwell2Selection}
                        handleSwell1Angle={handleSwell1Angle}
                        handleSwell2Angle={handleSwell2Angle}
                        handleSwell1Height={handleSwell1Height}
                        handleSwell2Height={handleSwell2Height}
                        handleSwell1Interval={handleSwell1Interval}
                        handleSwell2Interval={handleSwell2Interval}
                        handleStarSelection={handleStarSelection}
                        handleDistanceSelection={handleDistanceSelection}
                        pause={pause}
                        setStatus={setStatus}
                        tideNow={tideNow}
                        data={data}
                        tideDisplay={tideDisplay}
                    />
                </ConditionsContext.Provider>
                <div className=''>
                    <div className='containerBox bold color-yellow bg-lite p-20'>
                        <CollapseToggleButton
                            title={`${icons.wave} WAVE SUMMARY`}
                            isCollapsed={matchCollapse}
                            setCollapse={setMatchCollapse}
                            align='left'
                        />
                    </div>
                    {
                        (matchCollapse)
                        ? <div></div>
                        : <div>
                            <div className='m-10 mt-20'>
                                <span className='bold color-lite'>
                                            {(getCount() === 1) ? `1 wave` : `${getCount()} waves`}
                                </span> of {locations.length}
                            </div>
                            <div className='m-5'>
                                <span className='bold color-lite'>{status.distance} mile</span> radius
                            </div>
                            <div className='m-5 bold'>
                                prefer:
                            </div>
                            <div className='m-5'>
                                <span className='bold color-neogreen'>{status.swell1Direction} </span>and <span className='color-orange bold'>{status.swell2Direction} </span>swell
                            </div>
                            <div className='m-5'>
                                <span className='bold color-lite'>{status.tide} </span>tide
                            </div>
                            <div className='m-5'>
                                <span className='bold color-lite'>{status.windDirection}</span> winds.
                            </div>
                        </div>

                    }
                </div>
                <div className='containerBox bold color-yellow bg-lite p-20'>
                    <CollapseToggleButton
                        title={
                            `${
                                (locationCollapse) 
                                ? '+ EXPAND'
                                : '- COLLAPSE'
                            } ALL`
                        }
                        isCollapsed={locationCollapse}
                        setCollapse={setLocationCollapse}
                        align='left'
                    />
                </div>
                <div className={`mt-20 ${(edit)?'':'mb-40'}`}>
                    {sortedSpots()}
                </div>
                <div className='mb--10'>
                        <WaveUtils status={status} item={status} logLocation={() => alert('needs work')} setEdit={setEdit} updateLocations={updateLocations}></WaveUtils>
                </div>
                {/*
                    <div className='flexContainer t-50 mt-10'>
                        <div className='flex4Column'>{swell1Button()}</div>
                        <div className='flex4Column'>{swell2Button()}</div>
                        <div className='flex4Column'>{tideButton()}</div>
                        <div className='flex4Column'>{windButton()}</div>
                    </div>
                */}
            </div>
            {/*<div className={`bt-5 r-20 bg-tinted ${(buttonsVisible) ? 'width-100-percent' : 'w-75'}`}>*/}
                {/* <div className={`button-container mr-5 ml-70 mt-5 ${(buttonsVisible) ? 'show-buttons' : 'hide-buttons'}`}> */}
            <div className={`containerBox bt--20 width--60`}>
                <div className={`button-container mt-5`}>
                    <div className='scrollSnapRight'>
                        {currentTideButton()}
                    </div>
                    <div className='scrollSnapRight'>
                        {currentButton()}
                    </div>
                    <div className='scrollSnapRight'>
                        {conditionsButton()}
                    </div>
                    <div className='scrollSnapRight mr-10'>
                        {repositionButton()}
                    </div>
                    <span className='containerBox button scrollSnapRight' onClick={handleEditToggle}>{icons.edit}</span>
                    <div className='containerBox button scrollSnapRight' onClick={addWave}>
                        <span className='mr-5 text-outline-light button'>{icons.plus}</span>
                        <span className=''>{icons.wave}</span>
                    </div>
                </div>
                {/*
                    <div className='icon-container r-20 button box-shadow mb-10 ml-15' onClick={toggleButtons}>
                        <span className='repositon-icon'>🚀</span>
                    </div>  
                */}
            </div>
            {/*
                <div className={`containerDetail flexContainer rt-5 bt-5 w-200`}>
                    <span className='containerBox flex2Column button' onClick={handleEditToggle}>{icons.edit}</span>
                    <div className='containerBox flex2Column button' onClick={toggleAddWave}>
                        <span className='mr-5 text-outline-light button'>{icons.plus}</span>
                        <span className=''>{icons.wave}</span>
                    </div>
                </div>
            */}
        </div>
    )
}
export default Waves;