import React from 'react';
import getKey from '../utils/KeyGenerator.js';

const FunctionalSelector = ({
    items, 
    label, 
    groupTitle, 
    selected, 
    padding, 
    fontSize, 
    maxWidth, 
    width,
    bgColor=null,
    color=null,
    onChange
}) => {
    
    const setSelected = (item) => {
        selected = item;
        return tag(item)
    }
    
    const tag = (item) => <option key={getKey(item)} value={item}>{item}</option>;
    const getTag = (item, index) => (Number(item) === Number(selected)) ? setSelected(item) : tag(item);
    const selectItems = items.map((item, index) => getTag(item,index));
    const handleChange = (event) => {
        selected = event.target.value;
        console.log(`FunctionalSelector => selected: ${selected}`)
        onChange(groupTitle, label, selected);
    }
    
    return (
        <label key={getKey('selectorContainer')}>
            <select className={`containerBox contentCenter button width-100-percent ${color || 'color-soft'} ${ bgColor || 'bg-tintedMediumDark'}`} value={(selected)||''} onChange={handleChange}>
                {selectItems}
            </select>
        </label>
    );
}

export default FunctionalSelector;