import React, { createContext, useEffect, useState, useContext } from 'react';

import validate from '../utils/validate';
import initializeData from '../utils/InitializeData';
import parsDefault from '../games/scorekeeper/parsDefault';

export const GolfContext = createContext();

const GolfParent = ({
    children,
    targetElementRef
}) => {

    const [golfPars, setParData] = useState();
    const [edit, setEdit] = useState(false);

    const setPars = (newValue) => {
        setParData(newValue);
    };
    
    useEffect(() => {
        const localPars = initializeData('golfPars', parsDefault);
        console.log(`useEffect[] localPars: ${JSON.stringify(localPars, null, 2)}`);
        const newPars = [...localPars];
        console.log(`useEffect[] newPars: ${JSON.stringify(newPars, null, 2)}`);
        setPars(newPars);
    }, []);

    useEffect(() => {
        if (JSON.stringify(golfPars) !== '[]' && validate(golfPars) !== null) {
            console.log(`useEffect[golfPars] golfPars: ${JSON.stringify(golfPars, null, 2)}`);
            localStorage.setItem('golfPars', JSON.stringify(golfPars));
            setEdit(false);
        }
    }, [golfPars]);
    useEffect(() => {
        console.log(`edit: ${edit}`);
    }, [edit]);

    return (
        
        <GolfContext.Provider value={{
            golfPars,
            setPars,
            targetElementRef
        }}>
            {
                (validate(golfPars) !== null)
                    ? children
                    : <div>WHOOOPSIE!</div>
            }
        </GolfContext.Provider>
    );
};
export const usePlayers = () => useContext(GolfContext);

export default GolfParent;